      tabComponentMap: {
        Home: 'HomeTab',
        'New Launches': 'AllProducts',
        All: 'AllProducts'
      }
    }),
    watch: {
      arrayCart(nV){
        const len = nV.length
        this.$emit('cartSize', len)
        this.generateWhatsappText()
        return len
      },
      isGift(){
        this.giftMessage = ''
      },
      giftMessage() {
        this.generateWhatsappText()
      }
    },
    computed: {
      recentProducts() {
        return this.items.filter(i=>i.new)
      },
      whatsappLink() {
      return `https://wa.me/${9158186936}?text=${encodeURIComponent(this.whatsappMessage)}`;
      },
      arrayCart(){
        return Object.values(this.cart)
      },
      isCartEmpty(){
        return this.arrayCart.length === 0
      },
      totalCartValue(){
        return this.arrayCart.reduce((acc,item)=>item.price*item.quantity+acc, 0 )
      }
    },
    methods: {
      setFilter(value){
        this.filterText = value
      },
      getItems(item){
        if(item === 'All'){
          if(this.filterText){    
            // Escape special characters in filterText to prevent regex errors
            const escapedFilterText = this.filterText.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');

            // Create a case-insensitive regex for partial matching
            const regex = new RegExp(escapedFilterText, 'i');

            return this.items.filter(perfume => regex.test(perfume.name) || regex.test(perfume.description)); 
          }
          return this.items
        } 
        return this.recentProducts
      },
      getComponent(key){
        return this.tabComponentMap[key]
      },
      addToCart(perfume){
         Vue.set(this.cart,perfume.id,{...perfume, quantity: 1})
         this.notification = true
      },
      removeFromCart(perfume){
        Vue.delete(this.cart, perfume.id);
      },
      closeDialog(){
        this.$emit('closeDialog')
      },
      decrementQuantity(perfume){
        const currentQuantity = perfume.quantity
        if(currentQuantity<=1) return
        Vue.set(this.cart,perfume.id,{...perfume, quantity: perfume.quantity - 1})
      },
      incrementQuantity(perfume){
        Vue.set(this.cart,perfume.id,{...perfume, quantity: perfume.quantity + 1})
      },
      generateWhatsappText() {
          let billText = "```"; // Start monospace block for WhatsApp

          // Add table headers with spacing for alignment
          billText += "No. Name        Qty  Price  Total\n";
          billText += "---------------------------------\n";

          // Loop through the cart items
          this.arrayCart.forEach((item, index) => {
              const total = item.price * item.quantity;

              // Split name to fit into 12 characters
              let nameLine1 = item.name.substring(0, 12); // First line of name (max 12 chars)
              if(nameLine1.length<12) nameLine1.padEnd(12-nameLine1.length)
              let nameLine2 = item.name.length > 12 ? item.name.substring(12) : ''; // Second line if name is longer

              // Prepare table rows: Align columns manually
              billText += `${index + 1}.${nameLine1.padEnd(5)}   ${String(item.quantity)}   ${String(item.price)}   ${String(total)}\n`;
              
              // If the name is longer, add the second line of the name
              if (nameLine2) {
                  billText += `  -${nameLine2.padEnd(3)}\n`; // Indentation for second line
              }
          });

          // Add the total amount
          const totalAmount = this.arrayCart.reduce((total, item) => total + (item.price * item.quantity), 0);
          billText += "\n*Total:*".padEnd(25) + totalAmount.toString().padStart(6) + "\n";

          // End monospace block
          billText += "```";

          // Add gift message if applicable
          if (this.isGift) {
              billText += `\n*Gift Text:* ${this.giftMessage}`;
          }

          // Assign the formatted message
          this.whatsappMessage = billText;
      }

